import React from 'react';
import "./services.css";
import image1 from "../../assets/service1.png";
import image2 from "../../assets/service2.png";
import image3 from "../../assets/service3.png";


const data=[
  {
    id: 1,
    image: image1,
    title: "Web Developer",
    description:
    " Hi there i'm a FrontEnd develpoer ",
  }, 

  {
    id: 2,
    image: image2,
    title:"Online vehicles Services",
    description:
    "My Online Vechiles Services help to shifting your Flats , Offices ,Shopes etc.  "
  },
  {
    id: 3,
    image: image3,
    title: "Voice Assistance ",
    description:
    "Voice Assistance developer from python ",
  }
  

];

const Services = () => {
  return(
    <section className="services__container section " id='services'>
      <h2 className="section__title">Services </h2>
  
  
      <div className='services__container grid'>
        {data.map(({id, image, title, description}) => {
         return (
          <div className= "services__card" key={id}>
            <img src={image} alt="" className="services__img" />
            <h3 className='services__title'>{title}</h3>
            <p className='services__description'>{description}</p>
          </div>
         )
        }) }
  
      </div>
    </section>
    );
  
};

export default Services
