import React from 'react';
import "./home.css";
import Me from "../../assets/logo.png"
import HeaderSocials from './HeaderSocials';


const Home = () => {
  return (
    <section className="home__container" id="home">
      <div className='intro'>

        <h1 className='home__name'>I'm Sanjiv <span>Shah</span> </h1>
        <p className='home__p'>Frontend Doveloper<br></br> This Is my official Portfolio website </p>
        <HeaderSocials />

        <a href='#contact' className='btn'>Touch Me</a>
      </div>
      <div className='images'>
        <img src={Me} alt='' className='home__image' />
      </div>
    </section>
  )
}

export default Home
