import React from 'react';
import "./about.css";
import image from "../../assets/about.jpeg"
import AboutBox from './AboutBox';

const About = () => {
  return (
      <section className="about__container section" id='about'>
        <h2 className='section_title'>About Me</h2>
        <div className='about__container grid'>
          <img src={image} alt='' className="about__img" />
          <div className="about__data grid">
            <div className="about__info">
              <p className="about__description">
                Hi I am Sanjiv Shah,Developer from Kathmandu,Nepal.I have rich experiences in Web Application developer in FrontEnd
                but learning in BackEnd in Django, Node-JS For DataBase Mysql, MongoDB and python develpoer, also I ma good in  marketing analysis.
                My project Car rental system from HTML, CSS, JavaScript and PHP. My python3 project is Assistance name is Friday
              </p>
              <a href="#home" className="btn">Download CV</a>
            </div>
            <div className="about_skills grid">
              <div className="skills__data">
                <div className="skills__tittle">
                  <h3 className="skills__name"> HTML</h3>
                  <span className="skills__numbers">80%</span>
                </div>
                <div className="skills__bar">
                  <span className="skills__percentage developments"></span>
                </div>
              </div>
              <div className="skills__data">
                <div className="skills__tittle">
                  <h3 className="skills__name">CSS</h3>
                  <span className="skills__number">70%</span>
                </div>
                <div className="skills__bar">
                  <span className="skills__percentage css"></span>
                </div>
              </div>
              <div className="skills__data">
                <div className="skills__tittle">
                  <h3 className="skills__name">React js</h3>
                  <span className="skills__number">60%</span>
                </div>
                <div className="skills__bar">
                  <span className="skills__percentage react  "></span>
                </div>
              </div>
              <div className="skills__data">
                <div className="skills__tittle">
                  <h3 className="skills__name">Python </h3>
                  <span className="skills__number">50%</span>
                </div>
                <div className="skills__bar">
                  <span className="skills__percentage python  "></span>
                </div>
              </div>
            </div>
          </div>
        </div>
        <AboutBox />
      </section>

  )
}

export default About
