import React from 'react';
import "./contact.css";


const Contact = () => {
    return (
        <>
            <section id="contact">

                <h1 className="section-header">Contact</h1>

                <div className="contact-wrapper">

                    <form id="contact-form" className="form-horizontal" role="form">

                        <div className="form-group">
                            <div className="col-sm-12">
                                <input type="text" className="form-control" id="name" placeholder="FullName" />
                            </div>
                        </div>

                        <div className ="form-group">
                            <div className="col-sm-12">
                                <input type="email" className="form-control" id="email" placeholder="Email" />
                            </div>
                        </div>
                        <div className ="form-group">
                            <div className="col-sm-12">
                                <input type="number" className="form-control" id="number" placeholder="Number" />
                            </div>
                        </div>

                        <textarea className="form-control" rows="10" placeholder="MESSAGE" name="message" required></textarea>

                        <button className="btn btn-primary send-button" id="submit" type="submit" value="SEND">
                            <div className="alt-send-button">
                                <i className='send_icon' class="fa fa-paper-plane"></i><span className="send-text">SEND</span>
                            </div>

                        </button>

                    </form>

                    <div className="direct-contact-container">

                        <ul className="contact-list">
                            <li className="list-item"><i class="fa fa-map-marker fa-2x"><span className="contact-text place">Kathmandu, Bagmati</span></i></li>

                            <li className="list-item"><i class="fa fa-phone fa-2x"><span className="contact-text phone"><a href="tel:+9779826297834" title="Give me a call">+9779826297834</a></span></i></li>

                            <li classname="list-item"><i class="fa fa-envelope fa-2x"><span className="contact-text gmail"><a href="mailto:#" title="Send me an email">sahsanjiv111@gmail.com</a></span></i></li>

                        </ul>

                        <div>
                            <ul className="social-media-list">
                                <li><a href="#"  className="contact-icon">
                                    <i class="fa-brands fa-instagram" aria-hidden="true"></i></a>
                                </li>
                                <li><a href="#"  className="contact-icon">
                                    <i class="fa-brands fa-facebook" aria-hidden="true"></i></a>
                                </li>
                                <li><a href="#" className="contact-icon">
                                    <i class="fa-brands fa-twitter" aria-hidden="true"></i></a>
                                </li>
                                <li><a href="#"  className="contact-icon">
                                    <i class="fa-brands fa-github" aria-hidden="true"></i></a>
                                </li>
                            </ul>
                           
                        </div>
                    </div>
                </div>

            </section>
        </>
    )
}

export default Contact
